import React, { Component } from 'react';
import '../css/category.css';

class category extends Component {
  state = {
    category : [
      {
        name : "",
        value : "category1",
        number : 1,
      },
      {
        name : "",
        value : "category2",
        number : 2,
      },
      {
        name : "()",
        value : "category3",
        number : 3,
      },
      {
        name : "()",
        value : "category4",
        number : 4,
      },
      {
        name : "()",
        value : "category5",
        number : 5,
      },
      {
        name : "()",
        value : "category6",
        number : 6,
      },
      {
        name : ", , ",
        value : "category6",
        number : 7,
      }
    ],
  };

  render() {
    return (
      <div className="category">
        <div className="category-menu">{this.state.category.map(cate => (
        <a href="#">{cate.name}</a>
        ))}
      </div>
      </div>
    );
  }
}

export default category;